
.welcome {
  margin: 0 0 30px;
  text-align: center;
  font-style: italic;
}

.welcome p {
  margin: 0;
  display: inline-block;
}

.welcome .photo-small img {
  display: inline-block;
  width: 200px;
  height: 200px;
  margin: 0 0 20px 0;
  padding: 16px;
  position: relative;
  background: #fff;
  border: 2px solid #d2d3d6;
  border-bottom-color: #c5c6ca;
  border-radius: 116px;
  -webkit-box-shadow: 0 4px 12px rgba(33,33,33,0.05);
          box-shadow: 0 4px 12px rgba(33,33,33,0.05);
  vertical-align: top;
}

.social-media {
  margin: 0 0 14px;
  text-align: center;
  font-size: 0;
}

.social-media li {
  display: inline;
  padding: 0;
}

.social-media a {
  display: inline-block;
  width: 46px;
  height: 46px;
  margin: 0 7px 10px;
  overflow: hidden;
  position: relative;
  background-image: url(data:image/svg+xml;base64,PD94bWwgdmVyc2lvbj0iMS4wIiA/PjxzdmcgeG1sbnM9Imh0d…IxIiBoZWlnaHQ9IjEiIGZpbGw9InVybCgjZ3JhZC11Y2dnLWdlbmVyYXRlZCkiIC8+PC9zdmc+);
  background-image: -webkit-linear-gradient(top, #fff 0%, #fafafa 100%);
  background-image: -o-linear-gradient(top, #fff 0%, #fafafa 100%);
  background-image: -webkit-gradient(linear, left bottom, left top, from(#fff), to(#fafafa));
  background-image: -webkit-linear-gradient(bottom, #fff 0%, #fafafa 100%);
  background-image: -o-linear-gradient(bottom, #fff 0%, #fafafa 100%);
  background-image: linear-gradient(to top, #fff 0%, #fafafa 100%);
  border: 1px solid #c2c3c7;
  border-bottom-color: #b7b9be;
  border-radius: 48px;
  -webkit-box-shadow: inset 0 1px 0 rgba(255,255,255,0.8), inset 0 0 0 1px rgba(255,255,255,0.5), 0 1px 1px rgba(33,33,33,0.05);
          box-shadow: inset 0 1px 0 rgba(255,255,255,0.8), inset 0 0 0 1px rgba(255,255,255,0.5), 0 1px 1px rgba(33,33,33,0.05);
  -webkit-tap-highlight-color: transparent;
}

.social-media a [class^=icon-] {
  display: block;
  width: 100%;
  height: 100%;
  margin: 0;
  text-align: center;
  font-size: 22px;
  line-height: 46px;
  color: #9da1aa;
  text-shadow: 0 1px 0 #ffffff;
}

.social-media a::after {
  content: "";
  opacity: 0;
  width: 80px;
  height: 80px;
  position: absolute;
  top: -56px;
  left: -33px;
  background-image: -webkit-linear-gradient(-35deg, rgba(255,255,255,0.7) 0%, rgba(255,255,255,0) 60%);
  background-image: -o-linear-gradient(-35deg, rgba(255,255,255,0.7) 0%, rgba(255,255,255,0) 60%);
  background-image: -webkit-linear-gradient(145deg, rgba(255,255,255,0.7) 0%, rgba(255,255,255,0) 60%);
  background-image: -o-linear-gradient(145deg, rgba(255,255,255,0.7) 0%, rgba(255,255,255,0) 60%);
  background-image: linear-gradient(-55deg, rgba(255,255,255,0.7) 0%, rgba(255,255,255,0) 60%);
  -webkit-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

.social-media a:hover {
  background: #444;
  border-color: #828692;
  -webkit-box-shadow: inset 0 1px 2px rgba(0,0,0,0.1), inset 0 0 1px rgba(0,0,0,0.1);
          box-shadow: inset 0 1px 2px rgba(0,0,0,0.1), inset 0 0 1px rgba(0,0,0,0.1);
  text-decoration: none;
}



